<template>
  <section v-if="items.length > 0">
    <BotUI v-for="item in items" :item="item" :key="item"/>
  </section>
  <section v-else class="m-5">
    <div class="alert alert-warning">
      <p>Önce hangi ürünlerin etkilencek bana bildirmen lazım</p>
      <p>Bildirmek için
        <router-link to="/about">Etkilenecek Ürünler</router-link>
        sayfasına git.
      </p>
    </div>
  </section>
</template>

<script>
import BotUI from '@/components/BotUI.vue'

export default {
  name: 'HomeView',
  components: {
    BotUI
  },
  computed: {
    items () {
      return this.$store.state.items
    }
  },
  mounted () {
    this.$store.commit('setItems', JSON.parse(localStorage.getItem('items')))
  }
}
</script>
