import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // productlarımızı tutacağımız bir array oluşturuyoruz
    items: [],
    // jwt tokenımızı tutacağımız bir değişken oluşturuyoruz
    jwt: null
  },
  getters: {
    getJwt: state => {
      return state.jwt
    },
    isAuthenticated: state => {
      return state.jwt !== null
    },
    items: state => {
      return state.items
    }
  },
  mutations: {
    setJwt: (state, jwt) => {
      state.jwt = jwt
    },
    setItems: (state, items) => {
      state.items = items
    },
    addItem: (state, item) => {
      state.items.push(item)
    },
    removeItem: (state, item) => {
      const index = state.items.findIndex(i => i.barcode === item.barcode)
      state.items.splice(index, 1)
    }
  },
  actions: {}
  // modules: {}
})
